import {post, get} from '../utils/https'//引用
const test_api = {
    https_test_post: function (url, data, back,backErr) {
        data = JSON.stringify(data);
        post(url, data).then((res) => {
            back(res)
        }).catch((err)=>{
            backErr(err)
        })
    }
}
export default test_api



