<template>
    <div class="install_guide">
        <Topheader></Topheader>
        <div class="guide_item">
            <div class="item_title">webpack</div>
            <div class="item_cont">
                webpack.config.js项目配置文件 entry入口文件entry: { //bundle1: './main.js',// 第一个JS文件 bundle2:
                './main1.js' // 第二个JS文件 output: { filename: '[name].js' / 输出文件命名} }, name对应entry中key-value中的key值
            </div>
            <div class="item_title">sass安装</div>
            <div class="item_cont">
                <span>npm install node-sass --save-dev</span>
                <br/>
                <span>npm install sass-loader@7.3.1 --save-dev</span>
                <br/>
                <span>npm install style-loader --save-dev </span>
                <br/>
                <span>webpack.base.conf.js中module.rules中插入{test:/\.scss$/,loaders:["style","css","sass"]}</span>
            </div>
            <div class="item_title">el-ui安装引入</div>
            <div class="item_cont">
                npm i element-ui -S
                <br/>
                完整引入 zIndex 初始弹框的z-index
                import 'element-ui/lib/theme-chalk/index.css';
                import ElementUI from 'element-ui';
                Vue.use(Element, { size: 'small', zIndex: 3000 });
                按需引入
                import { Button } from 'element-ui';
                Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 };
                Vue.use(Button);
            </div>
            <div class="item_title">vuex状态管理</div>
            <div class="item_cont">
                npm install --save vuex
                注入实例 main.js new Vue中注入store 调用$store
            </div>
            <div class="item_title">Jquery安装</div>
            <div class="item_cont">
                npm install jquery --save-dev <br/>
                build/webpack.base.conf.js中， 加入：const webpack = require('webpack'); <br/>
                在module.exports中加入 plugins: [new
                webpack.ProvidePlugin({$:"jquery",jQuery:"jquery","window.jQuery":"jquery"})] <br/>
                import $ from 'jquery'全局引入
            </div>
            <div class="item_title">百度地图插件</div>
            <div>npm install vue-baidu-map --save<br>
                import Vue from 'vue'
                import BaiduMap from 'vue-baidu-map'
                Vue.use(BaiduMap, {
                ak: 'YOUR_APP_KEY'
                })
            </div>
            <div class="item_cont"><i class="iconfont">&#xe61d</i></div>
        </div>
    </div>
</template>

<script>
    import Topheader from "@/components/TopHeader";
    import test_api from "../api/testAPI"

    export default {
        name: "homePage"
        , created() {
            test_api.https_test_post("/cityjson?utf-8",'',function (res) {
                console.log(res)},function (err) {
                console.log(err)
            })
            // this.$post("/cityjson").then((res)=>{
            //     console.log(res)
            // }).catch((err)=>{
            //     console.log(err)
            // })
        }
        , components: {
            Topheader: Topheader
        }
    };
</script>

<style lang="scss">
    .install_guide {
        .guide_item {
            .item_title {
            }
        }
    }
</style>



